/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */

@include ipad-portrait {

    .woocommerce-page .edgtf-content,
    div.woocommerce {

        .edgtf-quantity-buttons {

            .edgtf-quantity-input,
            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                font-size: 13px;
            }

            .edgtf-quantity-input {
                height: 28px;
                width: 28px;
                margin: 0 0 0 -5px;
                line-height: 28px;
            }

            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                width: 28px;
                height: 28px;
                line-height: 28px;
            }
        }
    }
}

@include phone-portrait {
    
    .woocommerce-page .edgtf-content,
    div.woocommerce {
        
        .edgtf-quantity-buttons {
	        
            .edgtf-quantity-input {
                width: 20px;
            }
            
            .edgtf-quantity-minus,
            .edgtf-quantity-plus {
                width: 20px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */